  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.unselectable {
