/* PRICE section */

.price__title {
  font-family: Caveat Brush, sans-serif;
  font-size: 24px;
  line-height: 1.25;
  color: var(--main-text-color-black);
  text-align: center;

  margin-bottom: 16px;
}

.price-cards__item {
  padding: 20px;
  margin-left: auto;
  margin-right: auto;

  background-color: var(--bg-color-section);
  border-radius: 40px;

  background-image: url(../images/price/prc-splash-mb.png);
  background-repeat: no-repeat;
  background-position: top;
  background-size: contain;
}

.price-cards__item:first-child {
  margin-bottom: 12px;
}

.price-cards__item:nth-child(2) {
  margin-bottom: 32px;
}

.price-cards__item:last-child {
  margin-bottom: 16px;
}

.card__title {
  font-family: Montserrat, sans-serif;
  font-style: italic;

  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  color: var(--main-text-color-black);
  text-align: center;

  padding-top: 20px;
  margin-bottom: 16px;
}

.card-list {
  margin-bottom: 32px;
}

.card-list__item {
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  line-height: 1.22;
  color: #2e2520;
}

.price-cards:first-child {
  margin-bottom: 12px;
}

.card-list__item:not(:last-child) {
  margin-bottom: 12px;
}
